/**
   Animation Slider: Les flches sont caches de base, au survol, elles apparaissent en slide-in
*/

@mixin animationArrowSliderMoveIn($out-of-screen, $in-screen){
    overflow: hidden;
    .owl-nav{

         .owl-prev{
            left: $out-of-screen;
            opacity: 0;
        }
         .owl-next{
            right: $out-of-screen;
            opacity: 0;
        }

    }

    &:hover, &:focus{
        .owl-nav{

             .owl-prev{
                left: $in-screen;
                opacity: 1;
            }
             .owl-next{
                right: $in-screen;
                opacity: 1;
            }

        }
    }
} 

/**
    Animation slider int: de base les numros de page du slider sont cachs, au survol des flches, les numros de page fadent in
*/
%animation-slider-arrow-fade--wambrechies-duo{
    .owl-pagination{
        opacity: 0;
        transition: all, 0.5s;
    }
    &:hover, &:focus{
        .owl-pagination{
            opacity: 1;
        }
    }
}