%molecule-district-section{
    padding: 45px 75px;
    box-shadow: 0 0 10px rgba(#203146, 0.15);
    position: relative;
    background-color: white;
    border-radius: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include RWD(tablet){ 
        border-radius: 5px;
        padding: 45px;
    }
    @include RWD(mobile){
        padding: 45px 20px;
    }

    h1{
        @extend %text-section-title-hp;
        color: #31455d;
        margin-bottom: 40px;
    }

    p, ul {
        font-family: $opensans;
        font-size: 1.8rem;
        font-weight: 400;
        color: #31455d;
        margin-bottom: 20px;
    }

    ul {
        list-style: disc;
    }

    h2{
        @extend %text-h2;
        margin-bottom: 30px;
        text-align: center;
    }

    h3{
        @extend %text-section-title-hp;
        font-size: 2rem;
        font-weight: 600;
        margin-bottom: 10px;
        color: #31455d;
    }

    .lead{
        margin: 0px;
    }

    .error  {
        background-image: url( svg-bg-uri($svg-exclamation, #5c5c5c) );
        background-repeat: no-repeat;
        background-size: 20px;
        background-position: 15px 15px;
        background-color: #FFDDDE;
        padding: 15px 20px;
        padding-left: 60px;
        color: #505050;
        font-size: 18px;
        margin-bottom: 20px;
    }
}